'use client'

import { useState } from 'react'
import Link from 'next/link'
import { Heart, Home, Car, Trees, Package, Star, ArrowRight, Eye, MapPin } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'

export default function FavoritesPage() {
  const [favorites, setFavorites] = useState([
    {
      id: 1,
      title: 'Modern 3-Bedroom Apartment in Kiyovu',
      price: 450000,
      currency: 'RWF',
      type: 'rent',
      location: 'Kigali, Kiyovu',
      image: '/api/placeholder/400/300',
      category: 'houses',
      views: 234,
      likes: 45,
      featured: true
    },
    {
      id: 2,
      title: 'Toyota Land Cruiser 2022',
      price: 28000000,
      currency: 'RWF',
      type: 'sale',
      location: 'Kigali, Nyarugenge',
      image: '/api/placeholder/400/300',
      category: 'cars',
      views: 189,
      likes: 32,
      featured: true
    },
    {
      id: 3,
      title: 'Residential Plot - 500m² in Kacyiru',
      price: 15000000,
      currency: 'RWF',
      type: 'sale',
      location: 'Kigali, Kacyiru',
      image: '/api/placeholder/400/300',
      category: 'land',
      views: 156,
      likes: 28,
      featured: false
    }
  ])

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'houses': return Home
      case 'cars': return Car
      case 'land': return Trees
      default: return Package
    }
  }

  const removeFavorite = (id: number) => {
    setFavorites(favorites.filter(item => item.id !== id))
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">My Favorites</h1>
              <p className="text-gray-600 mt-2">
                Properties and items you've saved for later
              </p>
            </div>
            <div className="text-sm text-gray-500">
              {favorites.length} items saved
            </div>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {favorites.length === 0 ? (
          <div className="text-center py-16">
            <Heart className="h-16 w-16 text-gray-300 mx-auto mb-4" />
            <h2 className="text-2xl font-semibold text-gray-900 mb-2">
              No favorites yet
            </h2>
            <p className="text-gray-600 mb-8">
              Start browsing and save properties you're interested in
            </p>
            <Link href="/">
              <Button className="bg-red-600 hover:bg-red-700">
                Browse Listings
              </Button>
            </Link>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {favorites.map((item) => {
              const Icon = getCategoryIcon(item.category)
              return (
                <Card key={item.id} className="hover:shadow-lg transition-shadow group">
                  <div className="relative">
                    <div className="w-full h-48 bg-gray-200 rounded-t-lg flex items-center justify-center">
                      <span className="text-gray-500">Image Placeholder</span>
                    </div>
                    {item.featured && (
                      <div className="absolute top-2 left-2 bg-red-600 text-white px-2 py-1 rounded text-xs font-semibold">
                        FEATURED
                      </div>
                    )}
                    <button
                      onClick={() => removeFavorite(item.id)}
                      className="absolute top-2 right-2 bg-white/90 backdrop-blur-sm rounded-full p-2 opacity-0 group-hover:opacity-100 transition-opacity hover:bg-red-50"
                    >
                      <Heart className="h-4 w-4 text-red-600 fill-current" />
                    </button>
                  </div>
                  <CardContent className="p-4">
                    <div className="flex items-center gap-2 mb-2">
                      <div className="w-6 h-6 bg-red-100 rounded flex items-center justify-center">
                        <Icon className="h-3 w-3 text-red-600" />
                      </div>
                      <span className="text-xs text-gray-500 capitalize">{item.category}</span>
                    </div>
                    <h3 className="font-semibold text-gray-900 mb-2 line-clamp-2">
                      {item.title}
                    </h3>
                    <div className="flex items-center justify-between mb-3">
                      <span className="text-2xl font-bold text-red-600">
                        {item.currency} {item.price.toLocaleString()}
                      </span>
                      <span className="text-sm text-gray-500 capitalize">
                        {item.type}
                      </span>
                    </div>
                    <p className="text-gray-600 text-sm mb-3 flex items-center">
                      <MapPin className="h-4 w-4 mr-1" />
                      {item.location}
                    </p>
                    <div className="flex items-center justify-between text-sm text-gray-500">
                      <div className="flex items-center space-x-3">
                        <span className="flex items-center">
                          <Eye className="h-4 w-4 mr-1" />
                          {item.views}
                        </span>
                        <span className="flex items-center">
                          <Heart className="h-4 w-4 mr-1" />
                          {item.likes}
                        </span>
                      </div>
                      <Button size="sm" className="bg-red-600 hover:bg-red-700">
                        View Details
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              )
            })}
          </div>
        )}
      </div>
    </div>
  )
}
